          this.$alert('当前要删除的热力公司[<span style="color:red;font-size: 20px;">' + row.ci_company_name + '</span>]的热力所或换热站的数量不全为零，不可以删除', '删除提示', {
            dangerouslyUseHTMLString: true
          });
        }else {
          this.$confirm('请再次确认是否要删除[<span style="color:red;">' + row.ci_company_name + '</span>]该行记录?', '删除提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning',
            dangerouslyUseHTMLString: true,
            closeOnClickModal: false,
            closeOnPressEscape: false,

          }).then(() => {
            let _jsondata_ =   {
              ci_uuid:row.ci_uuid,
            } ;
            var p1 = this.customGlobal.postHttpClient(
              this.customGlobal.serverUrl.thermalPowerCompany_delete,
              //this.customGlobal.serverUrl.baseURI,
              this.customGlobal.serverUrl.orgBaseURI,
              {"Content-Type": "application/x-www-form-urlencoded"},
              this.qs.stringify({jsondata:JSON.stringify(_jsondata_)})
            );
            p1.then((res) => {
              console.log("异步调用成功返回:"+JSON.stringify(res));
              if(res.data.status=='success'){
                this.$message({
                  type: 'success',
                  showClose: true,
                  duration:3000,
                  dangerouslyUseHTMLString: true,
                  message: '<span style="color:black;">基础信息 ['+row.ci_company_name+'] 删除成功 </span>'
                });
                Bus.$emit('globalMsg',{curid:row.ci_uuid,target:{},method:'3'});
                this.reQueryValveCurrentData();
              }else{
                console.info("删除失败");
