              ci_latitude:that.companyForm.ci_latitude,
              ci_memo: that.companyForm.ci_memo,
              flag : that.companyForm.flag
            } ;
            const loading = this.$loading({
              lock: true,
              text: '提交中...',
              target: '.main',
              spinner: 'el-icon-loading',

              background: 'rgba(0, 0, 0, 0)'
            });
            if(that.companyForm.flag === '1'){

              var p1 = this.customGlobal.postHttpClient(
                this.customGlobal.serverUrl.thermalPowerCompany_add,
                //this.customGlobal.serverUrl.baseURI,
