                {"Content-Type": "application/x-www-form-urlencoded"},
                this.qs.stringify({jsondata:JSON.stringify(_jsondata_)})
              );
              p1.then((res) => {
                console.log("异步调用成功返回:"+JSON.stringify(res));
                if(res.data.status=='success'){
                  this.companyDialogFormVisible =false;//保存成功关闭dialog
                  loading.close();
                  this.$message({
                    type: 'success',
                    showClose: true,
                    duration:3000,
                    dangerouslyUseHTMLString: true,
                    message: '<span style="color:black;">基础信息 ['+that.companyForm.ci_company_name+'] 新增成功 </span>'
                  });
                  //热力公司增加树节点,1是同级，2是子级
                  //注意：targetNode中的id是自动生成且唯一,如果是创建第1层的热力公司，currentNodeId一定设置为0,layer一定设置为2
                  Bus.$emit('globalMsg',{curid:this.companyForm.ci_parent_company_id,target:{id:that.companyForm.ci_uuid,caption:that.companyForm.ci_company_name,type:2},method:'1',layer:'2'});

                  this.queryValveCurrentData();
                }else{
                  console.info("新增失败");
                  loading.close();
                  // this.companyDialogFormVisible =false;//保存失败关闭dialog
                  this.$alert(res.data.msg, '新增失败', {
                    confirmButtonText: '确定',
                    callback: action => {
                      console.info('data:'+JSON.stringify(res.data));
                      if(res.data.msg==='请登录认证'){
                        this.$router.push({path:'/login'});
                      }
                    }
                  });
                }

              },error=>{
                console.log("异步调用出错:"+JSON.stringify(error));
                loading.close();
                // this.companyDialogFormVisible =false;//保存失败关闭dialog
                this.$alert(JSON.stringify(error.message), '新增出错!', {
                  confirmButtonText: '确定',
                  callback: action => {

                  }
                });
              }).catch((res) => {
                console.log("异步调用失败返回:"+JSON.stringify(res));
                loading.close();
                // this.companyDialogFormVisible =false;//保存失败关闭dialog
                this.$alert(JSON.stringify(res.message), '新增出错', {
                  confirmButtonText: '确定',
                  callback: action => {

                  }
                });
              });
            }else{
              var p1 = this.customGlobal.postHttpClient(
                this.customGlobal.serverUrl.thermalPowerCompany_update,
                //this.customGlobal.serverUrl.baseURI,
                this.customGlobal.serverUrl.orgBaseURI,
                {"Content-Type": "application/x-www-form-urlencoded"},
                this.qs.stringify({jsondata:JSON.stringify(_jsondata_)})
              );
              p1.then((res) => {
                console.log("异步调用成功返回:"+JSON.stringify(res));
                if(res.data.status=='success'){
                  this.companyDialogFormVisible =false;//保存成功关闭dialog
                  loading.close();
                  this.$message({
                    type: 'success',
                    showClose: true,
                    duration:3000,
                    dangerouslyUseHTMLString: true,
                    message: '<span style="color:black;">基础信息 ['+that.companyForm.ci_company_name+'] 修改成功 </span>'
                  });
                  //热力公司增加树节点,1是同级，2是子级
                  //注意：targetNode中的id是自动生成且唯一,如果是创建第1层的热力公司，currentNodeId一定设置为0,layer一定设置为2
                  Bus.$emit('globalMsg',{curid:that.companyForm.ci_uuid,target:{id:that.companyForm.ci_uuid,caption:that.companyForm.ci_company_name,type:2},method:'2'});

                  this.queryValveCurrentData();
                }else{
                  console.info("修改失败");
                  loading.close();
                  // this.companyDialogFormVisible =false;//保存失败关闭dialog
                  this.$alert(res.data.msg, '修改失败', {
                    confirmButtonText: '确定',
                    callback: action => {
                      console.info('data:'+JSON.stringify(res.data));
                      if(res.data.msg==='请登录认证'){
                        this.$router.push({path:'/login'});
                      }
                    }
                  });
                }

              },error=>{
                console.log("异步调用出错:"+JSON.stringify(error));
                loading.close();
                // this.companyDialogFormVisible =false;//保存失败关闭dialog
                this.$alert(JSON.stringify(error.message), '修改出错!', {
                  confirmButtonText: '确定',
                  callback: action => {

                  }
                });
              }).catch((res) => {
                console.log("异步调用失败返回:"+JSON.stringify(res));
                loading.close();
                // this.companyDialogFormVisible =false;//保存失败关闭dialog
                this.$alert(JSON.stringify(res.message), '修改出错', {
                  confirmButtonText: '确定',
                  callback: action => {

                  }
                });
              });
            }
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      }
    }
  }
</script>

